@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie-list-items {
  color: whitesmoke;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  padding: 1vw;
}

.movie-list-items:hover {
  background-color: rgba(255, 102, 0, 0.041);
}

.from-ten > p {
  display: inline;
  color: rgba(128, 128, 128, 0.582);
  font-weight: 600 !important;
  font-size: 18px !important;
}

.movie-list-rate > h6 {
  padding: 1vw;
  font-weight: 700 !important;
  font-size: 26px;
  color: #ff6600;
  margin: 0 !important;
  font-weight: lighter;
}

.movie-list-name {
  font-family: "Oswald";
  font-weight: 600;
  font-size: 20px;
}

.movie-list-genre {
  color: rgba(128, 128, 128, 0.582);
}

.movie-list-cover {
  height: 45vh;
  width: 15vw;
}

.no-Content-Found {
  width: 100%;
  height: 40vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff6600;
}

.no-Content-Found h1 {
  font-size: 3vw;
}

h2 {
  color: whitesmoke;
  position: absolute;
  z-index: 5555555;
  top: 40vh;
  left: 3vw;
  font-size: 4vw !important;
  -webkit-filter: drop-shadow(1px 1px 1px black);
          filter: drop-shadow(1px 1px 1px black);
  font-family: monospace !important;
  font-weight: bold !important;
}

.svg-inline--fa {
  -ms-grid-row-align: center;
  align-self: center;
}

.movie-header-slider-ul {
  position: absolute;
  z-index: 5555;
  top: 49vh;
  left: 3vw;
  color: whitesmoke;
  display: flex;
}

.movie-header-slider-ul li {
  display: flex;
  padding: 10px;
  padding: 1vw;
  text-align: center;
  justify-content: center;
  list-style: none;
}

.movie-header-slider-ul li p {
  padding: 10px;
  margin: 0 !important;
}

#movies {
  width: 100%;
  overflow: hidden;
  scrollbar-width: none;
  background-color: #202020;
}

.movie-header {
  width: 100vw;
  height: 80vh;
  overflow: hidden;
}

.movie-header-slider {
  width: 100vw;
  height: 90vh;
}

.movie-header-slider-gradient {
  width: 100%;
  overflow: hidden;
  height: 80vh;
  position: absolute;
  top: 0;
  z-index: 555;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 50%, #0f0f0f 100%);
}

.movie-header-button {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 11.5vw;
  height: 7vh;
  border-radius: 4vw;
  border: none;
  position: absolute;
  z-index: 5555;
  top: 63vh;
  left: 0;
  color: whitesmoke;
  font-weight: bold;
  font-size: 16px;
}

.movie-header-button p {
  padding-left: 15px;
  -ms-grid-row-align: center;
  align-self: center;
  margin: 0 !important;
}

.watch {
  background-color: #ff6600;
  left: 3vw !important;
  transition: 0.2s all;
}

.watch:hover {
  background-color: whitesmoke;
  color: #ff6600;
}

.view {
  color: #ff6600;
  background-color: transparent;
  border: 2px solid whitesmoke;
  left: 17vw !important;
  transition: 0.2s background-color;
}

.view:hover {
  background-color: whitesmoke;
}

.movie-menu {
  width: 100vw;
  height: 10vh;
  background-color: #1a1616;
  display: flex;
  text-align: center;
}

.movie-menu-list-ul {
  display: flex;
  justify-content: left;
  text-align: center;
  margin: 0 !important;
  margin-left: 10vw !important;
  position: static !important;
}

.movie-menu-list-ul h6 {
  font-size: 18px;
  padding: 0 1vw;
  font-family: "Oswald", sans-serif;
  font-stretch: ultra-expanded;
  -ms-grid-column-align: center;
      justify-self: center;
  margin: auto !important;
}

.movie-menu-list-ul h6:hover {
  color: rgba(255, 102, 0, 0.438);
}

.movie-menu-list-li {
  margin: auto;
  cursor: pointer;
  list-style: none;
}

.movie-menu-search-box {
  position: relative;
  right: -5vw;
  margin-left: 20vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 20vw;
}

.movie-menu-search-form {
  height: 5vh;
  border: 2px solid #ff6600;
  border-radius: 2vw;
  overflow: hidden;
  margin: auto;
}

.movie-menu-search-form-text {
  border: none;
  padding: 0.6vw;
  color: whitesmoke;
  border-right: none;
  height: 5vh;
  background: transparent;
  caret-color: #ff6600;
}

.movie-menu-search-form-text:focus {
  outline: none;
}

.movie-menu-search-form-btn {
  color: #ff6600;
  background-color: transparent;
  border: none;
  height: 5vh;
}

.movie-menu-sort-box {
  position: relative;
  right: -5vw;
  cursor: pointer;
  width: 18vw;
  height: 5vh;
  color: #ff6600;
  background-color: transparent;
  margin: auto 0;
}

.movie-menu-sort-box h6 {
  margin: auto 10px !important;
}

.movie-menu-sort-btn {
  border: 2px solid #ff6600;
  width: 18vw;
  display: flex;
  justify-content: center;
  text-align: center;
  height: 5vh;
  border-radius: 2vw;
  display: flex;
}

.movie-menu-sort-box-open {
  display: none;
  position: absolute;
  background-color: #383737;
  width: 18vw;
  height: 0vw;
  z-index: 9999999;
  top: 5vh;
  right: 0;
  border-left: 2px solid #ff6600;
  border-right: 2px solid #ff6600;
  border-bottom: 2px solid #ff6600;
  border-bottom-right-radius: 2vw;
  border-bottom-left-radius: 2vw;
  overflow: hidden;
}

.movie-menu-sort-box-open-ul {
  color: #b8b3b3;
  padding-left: 0 !important;
}

.movie-menu-sort-box-open-ul li {
  padding: 1vw;
  list-style: none;
  border-bottom: 1px solid rgba(255, 102, 0, 0.445);
}

.movie-menu-sort-box-open-ul li:hover {
  border-bottom: 1px solid #ff6600;
  color: whitesmoke;
}

.movie-menu-sort-box-open-ul li p {
  margin: 0 !important;
}

.movie-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 99vw;
}

.carousel-control-prev,
.carousel-control-next {
  display: none !important;
}

.carousel-indicators {
  display: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

